.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;

  @include onTablet {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 30px;
  }

  @include onSmallDesktop {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 20px;
  }

  @include onDesktop {
    column-gap: 30px;
  }

  &--tablet {
    display: block;

    @include onTablet {
      display: grid;
    }
  }

  &--small-desktop {
    display: block;

    @include onSmallDesktop {
      display: grid;
    }
  }
  &--small-desktop-rows {
    @include onSmallDesktop {
      grid-template-rows: 100px 1fr;
    }
  }

  &__item {

    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        &--#{$start}-#{$end} {
          grid-column: #{$start} / #{$end + 1} ;
        }
      }
    }

    @include onTablet {
      @for $start from 1 through 6 {
        @for $end from $start through 6 {
          &--tablet-#{$start}-#{$end} {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }

    @include onSmallDesktop {
      @for $start from 1 through 12 {
        @for $end from $start through 12 {
          &--small-desktop-#{$start}-#{$end} {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }

      @for $start from 1 through 4 {
        @for $end from $start through 4 {
          &--small-desktop-row-#{$start}-#{$end} {
            grid-row: #{$start} / #{$end + 1};
          }
        }
      }
    }

    @include onDesktop {
      @for $start from 1 through 12 {
        @for $end from $start through 12 {
          &--large-screen-#{$start}-#{$end} {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }
}
